<template>
    <div>
        <el-form-item :label="field.name">
            <el-date-picker :type="field.datetime" v-model="formData[field.field]" range-separator="至" start-placeholder="开始时间" end-placeholder="结束时间" :value-format="field.datetime | time"></el-date-picker>
        </el-form-item>
    </div>
</template>
<script>
import { initFormData, timeFormat } from '@/utils/loader';
export default {
    name: 'datetime',
    data() {
        return {
            formData: initFormData( this.field.field, this.field.value )
        }
    },
    props: ['field'],
    filters:{
        time: function(val){
            return timeFormat[val];
        }
    },
    methods: {
        getElementData(){
            return this.formData
        },
    }
}
</script>




